<template lang="pug">
.dropdown(ref='dropdown' :class="{ show: visible }"
  @keydown.down.prevent.stop="navigateOptions('next')"
  @keydown.up.prevent.stop="navigateOptions('prev')"
  @keydown.enter.prevent.stop="handleKeyDownEnter")
  input.form-control.dropdown-toggle(type='text'
    ref='input'
    v-model.trim='searchString'
    :placeholder='placeholder'
    @blur='handleInputBlur'
    @click.prevent.stop='toggleOpen')
  button.clear-btn(type='button' @click="cancelSelect") &times;
  .dropdown-menu.p-0.w-100(ref='dropdown-menu' :class="{ show: visible }")
    a.dropdown-item.d-flex.justify-content-between(ref='dropdown-menu-item' v-for='(option, index) in suggestion'
      href='#'
      :title='option[displayKey]'
      :class='{ hover: hoverIndex === index, active: activeIndex === index }'
      @mouseenter='hoverIndex=index'
      @click.stop.prevent='selectOption(option)')
      span.dropdown-item-label {{ option[displayKey] }}
      span(v-if='option.hasOwnProperty("isLeaf") && !option.isLeaf') >
</template>

<script>
import { debounce } from './debounce'

export default {
  name: 'cf-select-input',
  props: {
    value: { // optionvalueKey
      type: [String, Number],
      default: ''
    },
    isRequesting: { // 
      type: Boolean,
      default: false
    },
    valueKey: { // option
      type: String,
      default: 'id'
    },
    displayKey: { // option
      type: String,
      required: true
    },
    options: { // 
      type: Array,
      default: () => []
    },
    placeholder: { // 
      type: String,
      default: ''
    },
  },
  data () {
    return {
      searchString: '', // 
      hoverIndex: -1, // Index
      activeIndex: -1, // 
      visible: false, // 
      suggestion: [], // 
    }
  },
  watch: {
    searchString: 'debounceFindMatch'
  },
  methods: {
    findMatch () { // 
      if (this.searchString.length > 0) {
        let regStr = new RegExp(this.searchString, 'i')
        let tmpArry = this.options.filter(ele => {
          return regStr.test(ele[this.displayKey])
        })
        if (tmpArry.length > 0) {
          // hover
          this.hoverIndex = -1
          this.suggestion = tmpArry
        } else {
          this.suggestion = [{
            [this.displayKey]: 'No available option',
          }]
        }
      } else { // 
        this.hoverIndex = -1
        this.suggestion = this.options
      }
    },
    debounceFindMatch: debounce(function () {
      this.findMatch()
    }, 200),
    toggleOpen () { // 
      if (!this.visible) {
        this.visible = true
        // 
        this.activeIndex = this.hoverIndex = this.options.findIndex(option => option[this.valueKey] === this.value)
        if (this.isRequesting) {
          this.suggestion = [{ //  options  loading
            [this.displayKey]: 'Loading...',
          }]
          let unwatch = this.$watch('options', () => {
            this.initSuggestion()
            unwatch()
          })
        } else {
          this.initSuggestion()
        }
      } else {
        this.visible = false
        this.$nextTick(() => {
          this.$refs['input'].blur()
        })
      }
    },
    initSuggestion () { //  suggestion
      this.suggestion = this.options
      this.$nextTick(() => {
        this.scrollIntoView()
      })
    },
    handleKeyDownEnter () { // 
      let selectedOption = this.suggestion[this.hoverIndex]
      if (selectedOption) {
        this.selectOption(selectedOption)
      }
    },
    reset () { // 
      this.searchString = ''
      this.visible = false
    },
    cancelSelect () { // 
      this.reset()
      this.$emit('select-change', null)
    },
    handleInputBlur (e) { // 
      let relatedTarget = e.relatedTarget || // divrelatedTargetnull
        e.explicitOriginalTarget || // FirefoxbuttonrelatedTargetnull
        document.activeElement; // IEFirefoxchromebody

      if (!relatedTarget || !this.$refs['dropdown'].contains(relatedTarget)) {
        let selectedOption = this.options.find(e => e[this.valueKey] === this.value)
        // input
        this.searchString = selectedOption ? selectedOption[this.displayKey] : ''
        this.visible = false
      }
    },
    /**
     * 
     * @param {object} option - 
     */
    selectOption (option) {
      let value = option[this.valueKey]
      if (value === undefined) {
        return
      }
      this.$emit('select-change', option)
      this.searchString = option[this.displayKey]
      this.toggleOpen()
    },
    /**
     * 
     * @param {string} direction -  prev/next
     */
    navigateOptions (direction) {
      // 
      if (!this.visible) {
        return this.visible = true
      }
      if (this.suggestion.length === 0) return
      if (direction === 'prev') {
        this.hoverIndex--
        if (this.hoverIndex < 0) {
          this.hoverIndex = this.suggestion.length - 1
        }
      } else if (direction === 'next') {
        this.hoverIndex++
        if (this.hoverIndex >= this.suggestion.length) {
          this.hoverIndex = 0
        }
      }
      this.scrollIntoView()
    },
    scrollIntoView () { // 
      if (this.hoverIndex === -1) return
      const container = this.$refs['dropdown-menu']
      const selected = this.$refs['dropdown-menu-item'][this.hoverIndex]
      const top = selected.offsetTop
      const bottom = top + selected.offsetHeight;
      const viewRectTop = container.scrollTop;
      const viewRectBottom = viewRectTop + container.clientHeight;

      if (top < viewRectTop) { // 
        container.scrollTop = top;
      } else if (bottom > viewRectBottom) { // 
        container.scrollTop = bottom - container.clientHeight;
      }
    }
  },
}
</script>

<style scoped lang="scss">
.clear-btn {
  opacity: 0;
  border: none;
  background-color: transparent;
  font-size: 20px;
  line-height: 1;
  padding: 6px 3px;
  position: absolute;
  top: 1px;
  right: 1px;
  transition: all ease .3s;
}
.dropdown:hover .clear-btn {
  opacity: 1;
}

.dropdown-menu {
  overflow-y: auto;
  max-height: 20rem;
}
.dropdown-item {
  padding: .5rem 1rem;
  cursor: pointer;
  .dropdown-item-label {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  &.active, &:active {
    background-color: #ffffff;
    color: #FA423E;
  }
  &.hover, &:hover {
    background-color: #fef6f5;
  }
}
</style>