<template lang="pug">
.position-relative
  slot
  .popover.fade(v-show='isShown' ref='popover' :class='popoverClass' :style='{"width": width}')
    .arrow
    h3.popover-header(v-if='title') {{ title }}
    .popover-body
      slot(name="content")
        | {{ content }}
</template>

<script>
export default {
  name: 'popover',
  props: {
    title: String, // popover 
    direction: { // popover/topleftrightbottom
      type: String,
      default: 'top',
      validator: value => ['top', 'left', 'right', 'bottom'].indexOf(value) > -1
    },
    trigger: { // clickfocushovermanual
      type: String,
      default: 'click',
      validator: value => ['click', 'focus', 'hover', 'manual'].indexOf(value) > -1
    },
    width: { // popover 
      type: String,
      default: '20rem'
    },
    content: { // slot
      type: String,
      required: false
    }
  },
  data () {
    return {
      isShown: false, //  popover 
    }
  },
  computed: {
    popoverClass () { //  direction  popover 
      let classArr = this.direction.split('-')
      classArr[0] = 'bs-popover-' + classArr[0]
      if (this.isShown) {
        classArr.push('show')
      }
      return classArr.join(' ')
    }
  },
  methods: {
    open () { // 
      this.$emit('popover-open')
      this.isShown = true
    },
    close () { // 
      this.$emit('popover-close')
      this.isShown = false
    },
    toggle () { // 
      this.$emit('popover-toggle')
      this.isShown = !this.isShown
    },
    initTrigger () { // 
      let defaultSlot = this.$slots.default
      if (defaultSlot) {
        let triggerDom = defaultSlot[0].elm //  dom 
        let popoverDom = this.$refs.popover

        // 
        //  beforeDestroy 
        switch (this.trigger) {
          case 'focus':
            triggerDom.setAttribute('tabindex', 0)
            triggerDom.addEventListener('focusin', this.open)
            triggerDom.addEventListener('focusout', this.close)
            this.$once('hook:beforeDestroy', () => {
              triggerDom.removeEventListener('focusin', this.open)
              triggerDom.removeEventListener('focusout', this.close)
            })
            break
          case 'click':
            let handleDocumentClick = e => { //  popover  dom  popover document  click 
              if (popoverDom.contains(e.target)) return
              this.close()
              document.removeEventListener('click', handleDocumentClick)
            }
            let handleTriggerClick = () => {
              this.toggle()
              //  setTimeout  popover  click  popover 
              setTimeout(() => {
                if (this.isShown) {
                  document.addEventListener('click', handleDocumentClick)
                }
              })
            }
            triggerDom.addEventListener('click', handleTriggerClick)
            this.$once('hook:beforeDestroy', () => {
              triggerDom.removeEventListener('click', handleTriggerClick)
            })
            break
          case 'hover':
            triggerDom.addEventListener('mouseenter', this.open)
            triggerDom.addEventListener('mouseleave', this.close)
            popoverDom.addEventListener('mouseenter', this.open)
            popoverDom.addEventListener('mouseleave', this.close)
            this.$once('hook:beforeDestroy', () => {
              triggerDom.removeEventListener('mouseenter', this.open)
              triggerDom.removeEventListener('mouseleave', this.close)
              popoverDom.removeEventListener('mouseenter', this.open)
              popoverDom.removeEventListener('mouseleave', this.close)
            })
            break
          case 'manual':
            triggerDom.addEventListener('click', this.open)
            this.$once('hook:beforeDestroy', () => {
              triggerDom.removeEventListener('click', this.open)
            })
            break
          default:
        }
      }
    }
  },
  mounted () {
    this.initTrigger()
  }
}
</script>

<style scoped lang="scss">
.popover {
  max-width: none;
}
/**/
%popover-horizontal-position {
  top: 50%;
  .arrow {
    top: 50%;
    transform: translateY(-50%);
    margin: 0;
  }
}
/**/
%popover-vertical-position {
  left: 50%;
  .arrow {
    left: 50%;
    transform: translateX(-50%);
    margin: 0;
  }
}
.bs-popover-left {
  @extend %popover-horizontal-position;
  left: -.5rem;
  transform: translate(-100%, -50%);
}
.bs-popover-right {
  @extend %popover-horizontal-position;
  left: 100%;
  transform: translateY(-50%);
}
.bs-popover-top {
  @extend %popover-vertical-position;
  top: -.5rem;
  transform: translate(-50%, -100%);
}
.bs-popover-bottom {
  @extend %popover-vertical-position;
  top: 100%;
  transform: translateX(-50%);
}
</style>
