<template lang="pug">
.tooltip-wrapper.position-relative
  slot
    i.fa.fa-question-circle
  .tooltip(:class='tooltipClass')
    .arrow
    .tooltip-inner.text-left(:style='{"max-width": maxWidth}')
      slot(name='content')
        | {{ content }}
</template>

<script>
export default {
  name: 'tooltip',
  props: {
    direction: { // tooltip/topleftrightbottom
      type: String,
      default: 'top',
      validator: value => ['top', 'left', 'right', 'bottom'].indexOf(value) > -1
    },
    content: { // slot
      type: String,
      required: false
    },
    maxWidth: { // 
      type: String,
      default: '20rem'
    }
  },
  computed: {
    tooltipClass () { //  direction  tooltip 
      let classArr = this.direction.split('-')
      classArr[0] = 'bs-tooltip-' + classArr[0]
      return classArr.join(' ')
    }
  }
}
</script>

<style scoped lang="scss">
.tooltip {
  display: none;
  /* tooltip  tooltip-inner  max-width */
  width: 20rem;
}
/**/
%tooltip-horizontal-position {
  top: 50%;
  .arrow {
    top: 50%;
    transform: translateY(-50%);
  }
}
/**/
%tooltip-vertical-position {
  left: 50%;
  .arrow, .tooltip-inner {
    left: 50%;
    transform: translateX(-50%);
  }
  .tooltip-inner {
    position: relative;
    display: inline-block;
  }
}

/*hover*/
.tooltip-wrapper:hover > .tooltip {
  opacity: 0.8;
  display: block;
  &.bs-tooltip-left {
    @extend %tooltip-horizontal-position;
    transform: translate(-100%, -50%);
    .tooltip-inner {
      float: right;
    }
  }
  &.bs-tooltip-right {
    @extend %tooltip-horizontal-position;
    left: 100%;
    transform: translateY(-50%);
    .tooltip-inner {
      float: left;
    }
  }
  &.bs-tooltip-top {
    @extend %tooltip-vertical-position;
    top: 0;
    transform: translate(-50%, -100%);
  }
  &.bs-tooltip-bottom {
    @extend %tooltip-vertical-position;
    transform: translateX(-50%);
  }
}
</style>
