export default {
  methods: {
    /**
     * url
     * ossurlurl
     * @param {string} url - 
     * @param {string} [filename] - 
     * @param {boolean} [openInNewPage] - 
     */
    downloadFileByUrl(url = '', filename = 'download', openInNewPage = true) {
      let linkId = 'download-hidden-link'
      let a = document.querySelector(`#${linkId}`)
      if (!a) { //  a firefox  a  document.body 
        a = document.createElement("A")
        a.id = linkId
        a.style = 'display: none;'
        // relwindows chrome69
        a.setAttribute('rel', 'noreferrer')
        document.body.appendChild(a)
      }
      /**
       *  png, pdf, jpg 
       * tab
       * http://jira.yuceyi.com/browse/WEB-366?focusedCommentId=11575&page=com.atlassian.jira.plugin.system.issuetabpanels%3Acomment-tabpanel#comment-11575
       */
      a.target = openInNewPage ? '_blank' : '_self'
      a.href = url
      a.download = filename
      a.click()
    },
    /**
     * 
     * 
     * @param {object} response - ajax
     */
    exportFile(response) {
      let blob = new Blob([response.data])
      let href = window.URL.createObjectURL(blob)
      let filename = response.headers['content-disposition'] // attachment;filename="JFKEDI.xls"
      // URL
      filename = decodeURI(filename.split('"')[1])
      this.downloadFileByUrl(href, filename, false)
    },
  }
}