import FullScreenImageViewer from './components/FullScreenImageViewer.vue';
import events from './components/vue-instance';

/* istanbul ignore next */
FullScreenImageViewer.install = function (Vue, args = {}) {
  // 
  const viewImgInFullScreen = function () {
    let len = arguments.length
    if (len) {
      events.$emit('show-img-viewer', {
        'images': arguments[0], // 
      })
    }
  }
  // 
  Vue.component(FullScreenImageViewer.name, FullScreenImageViewer);
  const name = args.name ? args.name : 'viewImgInFullScreen'
  // 
  Vue.prototype['$' + name] = viewImgInFullScreen
  // 
  Vue[name] = viewImgInFullScreen
};

export default FullScreenImageViewer;

