/**
 * 
 * @param  {function} fn - 
 * @param  {string} delay - 
 * @return {function} - 
 */
export function debounce(fn, delay) {
  //  timer
  let timer = null;
  //  timer
  return function () {
    //  'this'  'arguments'
    // 
    let context = this;
    let args = arguments;
    //  timer 
    clearTimeout(timer);
    timer = setTimeout(function () {
      fn.apply(context, args);
    }, delay);
  }
}
