<template lang="pug">
popover(ref='popover' trigger='manual' :direction="direction" @popover-open='openEditor')
  a.btn.btn-link.text-info.p-0(href='#' :title='editBtnTitle')
    u(v-if='textValue') {{ textValue }}
    i.fa.fa-pencil(v-else)
  div(slot='content')
    .d-flex.align-items-center
      //-  input  textarea 
      template(v-if='type === "textarea"')
        textarea.form-control.mr-2(rows='5', v-model.trim="text")
      template(v-if='type === "inputText"')
        input.form-control.mr-2(type='text', v-model.trim="text")
      button.btn.btn-info.mr-2(type='button', @click='submit', title='' :disable='isRequesting')
        i.fa.fa-check
      button.btn.btn-outline-dark(type='button', @click='closeEditor', title='')
        i.fa.fa-times
</template>

<script>
import Popover from './Index.vue'

export default {
  name: 'popover-edit',
  props: {
    'direction': { // popover/topleftrightbottom
      type: String,
      default: 'top'
    },
    'editBtnTitle': String, // title
    'type': { // textareainputText
      type: String,
      default: 'inputText'
    },
    'textValue': String, // 
  },
  components: { Popover },
  data () {
    return {
      text: '', // 
      isRequesting: false, // 
    }
  },
  methods: {
    openEditor () { // 
      // 
      this.text = this.textValue
    },
    closeEditor () {
      this.$refs['popover'].close()
    },
    submit () {
      this.isRequesting = true
      this.$emit('confirm', this.text, () => {
        this.isRequesting = false
        this.closeEditor()
      })
    }
  }
}
</script>
